import React from 'react';

import clsx from 'clsx';
import { FunctionalIcon } from '../icon/Icon';
import { FormFieldProps } from './form-field.interface';

export const FormField: React.FC<FormFieldProps> = ({
  messageId,
  required,
  children,
  state = 'default',
  message,
  ...props
}) => {
  return (
    <fieldset
      className={clsx('zep-inline-flex', 'zep-flex-col', 'zep-flex-nowrap')}
      data-testid="zep-form-fieldset"
      {...props}
    >
      {children}
      <span
        id={messageId}
        className={clsx(
          { 'zep-text-error': state === 'error' || required, 'zep-text-info': state === 'info' },
          'zep-mt-0.5 zep-flex  zep-flex-row zep-items-center zep-gap-0.5',
        )}
      >
        {(state === 'error' || required) && (
          <FunctionalIcon name="warning-triangle-filled" className="zep-text-error" />
        )}
        {state === 'info' && <FunctionalIcon name="info-circle-filled" className="zep-text-info" />}
        {message}
      </span>
    </fieldset>
  );
};
